@import"https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@300;400;500;700;900&display=swap";*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}:root{--color-midnight: #09090b;--color-deepblue: #0f172a;--color-neonblue: #38bdf8;--color-neoncyan: #06b6d4;--color-neonpink: #f472b6;--color-neonpurple: #c084fc}html,body{font-family:"Noto Sans SC",system-ui,-apple-system,sans-serif;background:var(--color-midnight);color:#e2e8f0;line-height:1.5;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}::-webkit-scrollbar{width:6px;height:6px}::-webkit-scrollbar-track{background:hsla(0,0%,100%,.05);border-radius:3px}::-webkit-scrollbar-thumb{background:hsla(0,0%,100%,.2);border-radius:3px}::-webkit-scrollbar-thumb:hover{background:hsla(0,0%,100%,.3)}.no-scrollbar{-ms-overflow-style:none;scrollbar-width:none}.no-scrollbar::-webkit-scrollbar{display:none}::selection{background:rgba(56,189,248,.3);color:#fff}button{font-family:inherit;cursor:pointer;border:none;background:none;color:inherit}img{max-width:100%;height:auto;display:block}a{color:inherit;text-decoration:none}ul,ol{list-style:none}