
.stats-panel {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.25rem 0.5rem;
  width: 100%;
  max-width: 20rem;
  animation: fadeIn 0.3s ease;
}

.stats-compact {
  max-width: 180px;
}

.stat-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.05);

  .stats-compact & {
    padding: 0.25rem;
    background: transparent;
    border: none;
  }
}

.stat-full {
  grid-column: span 2;
}

.stat-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.stat-label {
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  color: #94a3b8;

  .stats-compact & {
    font-size: 0.625rem;
  }
}

.stat-value {
  font-size: 0.875rem;
  font-weight: 700;
  font-family: ui-monospace, monospace;
  color: white;

  .stats-compact & {
    font-size: 0.75rem;
  }
}

.text-pink-400 { color: #f472b6; }
.text-green-400 { color: #4ade80; }
.text-cyan-400 { color: #22d3ee; }
.text-orange-400 { color: #fb923c; }
.text-rose-400 { color: #fb7185; }
.text-yellow-400 { color: #facc15; }
.text-purple-400 { color: #c084fc; }

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
