
.character-panel {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  max-width: 45%;
  transition: all 0.3s ease;
}

@media (min-width: 1024px) {
  .character-panel {
    max-width: 28rem;
  }
}

// ========== 名称标签 ==========
.name-badge {
  margin-bottom: 0.5rem;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  z-index: 20;
  max-width: 100%;
  position: relative;

  @media (min-width: 1024px) {
    padding: 0.5rem 1.5rem;
  }
}

.badge-player {
  background: rgba(6, 78, 59, 0.6);
  color: #a5f3fc;
}

.badge-enemy {
  background: rgba(76, 5, 25, 0.6);
  color: #fecdd3;
}

.name-text {
  font-weight: 700;
  font-size: 0.75rem;
  letter-spacing: 0.05em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @media (min-width: 1024px) {
    font-size: 1.125rem;
  }
}

.intention-hint {
  display: none;
  position: absolute;
  top: -2rem;
  right: 0;
  background: rgba(234, 179, 8, 0.1);
  border: 1px solid rgba(234, 179, 8, 0.5);
  color: #fef08a;
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  animation: pulse 2s ease-in-out infinite;
  align-items: center;
  white-space: nowrap;

  @media (min-width: 1024px) {
    display: flex;
  }
}

.intention-icon {
  margin-right: 0.25rem;
}

// ========== 头像 ==========
.avatar-container {
  position: relative;
  width: 100%;
  aspect-ratio: 1;
  max-width: 160px;
  margin-bottom: 0.5rem;
  cursor: help;

  @media (min-width: 1024px) {
    max-width: 280px;
    margin-bottom: 1.5rem;
  }

  &:hover {
    .avatar-glow {
      opacity: 0.4;
    }

    .avatar-image {
      filter: brightness(0.5) blur(2px);
    }

    .stats-overlay {
      opacity: 1;
    }
  }
}

.avatar-glow {
  position: absolute;
  inset: 0;
  border-radius: 1rem;
  filter: blur(30px);
  opacity: 0.2;
  transition: opacity 0.7s ease;

  @media (min-width: 1024px) {
    filter: blur(48px);
  }
}

.glow-player {
  background: #06b6d4;
}

.glow-enemy {
  background: #f43f5e;
}

.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.75rem;
  border: 2px solid;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
  z-index: 10;
  position: relative;
  transition: all 0.3s ease;

  @media (min-width: 1024px) {
    border-radius: 1rem;
  }
}

.avatar-player {
  border-color: rgba(6, 182, 212, 0.3);
}

.avatar-enemy {
  border-color: rgba(244, 63, 94, 0.3);
}

.avatar-pulse {
  animation: pulse 1s ease-in-out infinite;
}

.avatar-scale {
  transform: scale(1.05);
}

.avatar-climax {
  filter: brightness(1.25) saturate(2);
  animation: pulse 0.5s ease-in-out infinite;
}

.mobile-warning {
  display: block;
  position: absolute;
  top: -0.5rem;
  right: -0.5rem;
  z-index: 30;
  background: #eab308;
  color: black;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: pulse 1s ease-in-out infinite;
  border: 2px solid #fef08a;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);

  span {
    font-size: 0.625rem;
    font-weight: 700;
  }

  @media (min-width: 1024px) {
    display: none;
  }
}

.stats-overlay {
  position: absolute;
  inset: 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
  padding: 0.5rem;
  border-radius: 1rem;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.overlay-title {
  font-size: 0.625rem;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 0.25rem;
  font-weight: 700;

  @media (min-width: 1024px) {
    font-size: 0.75rem;
    margin-bottom: 0.5rem;
  }
}

// ========== 状态条 ==========
.bars-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0;

  @media (min-width: 1024px) {
    gap: 0.5rem;
    padding: 0 1rem;
  }
}

.climax-bar {
  width: 100%;
  display: flex;
  gap: 0.5rem;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}
